<template>
  <div>
    <br>
    <div style="margin-left: 40px"><h2>ip</h2></div>
    <br><br>
    <Row type="flex" justify="space-around">
      <Col span="20">
        <Card>
          <p slot="title">IP</p>
          <span style="text-align:center"><h2>IP{{ip}}</h2></span>
          <span style="text-align:center"><h2>{{city}}</h2></span>
        </Card>
      </Col>
    </Row>
    <br><br>
    <Row type="flex" justify="space-around">
      <Col span="20">
        <Card>
          <p slot="title">IP</p>
          <div style="text-align: center">
            <span
              style="font-size: 28px;font-family:Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,,Arial,sans-serif;">IP</span>
            <Input size="large" style="width: 300px"></Input>
            <Button size="large" type="success" @click="queryCity"></Button>
          </div>
          <br><br>
          <div style="text-align: center" v-if="this.searchCity">
            <span
              style="font-size: 28px;font-family:Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,,Arial,sans-serif;">{{searchCity}}</span>
          </div>
        </Card>
      </Col>
    </Row>
  </div>

</template>

<script>

  import $ajax from "axios";
  import Vue from "vue";

  export default {
    name: "ipUtil",
    data() {
      return {
        ip: "",
        city: "",
        searchIp: "",
        searchCity: ""
      }
    },
    methods: {
      //
      getClientInfo() {
        if (localStorage.hasOwnProperty("ip") && localStorage.hasOwnProperty("city")) {
          this.ip = localStorage.getItem("ip")
          this.city = localStorage.getItem("city")
        } else {
          $ajax.get('http://myip.ipip.net/', {}).then(
            res => {
              res.data = res.data.substring(6, res.data.length)
              this.ip = res.data.substring(0, res.data.indexOf("  "))
              this.city = res.data.substring(res.data.indexOf("  ")).replace("  ", "")
              localStorage.setItem("ip", this.ip)
              localStorage.setItem("city", this.city)
              this.uptVisitorNum()
            }
          )
        }

      },
      //
      uptVisitorNum() {
        $ajax.post(
          Vue.config.BaseUrl.ip + Vue.config.BaseUrl.uptVisitNum,
          {
            "ip": this.ip,
            "city": this.city
          },
          {}
        ).then(data => {
          // console.log("",data)
          if (data.data.status === 0) {
            localStorage.setItem("ip", this.ip)
            localStorage.setItem("city", this.city)
          }
        })
      },
      queryCity(){
        $ajax.get('/ip/ipNewV3?ip=36.130.31.195&key=f2bbbc87977dfc23066cf7d38f002897', {}).then(
          res => {
            console.log("ip",res)
            if(res.data.error_code===0){
              this.searchCity = res.data.result.Country+"-"+res.data.result.Province+"-"+res.data.result.City+"-"+res.data.result.Isp
            }else{
              this.$Message.error("IP")
            }
          }
        )
      }
    },
    created() {
      this.getClientInfo()
    }

  }
</script>

<style scoped>

</style>



