<template>
  <div class="me">
    <div class="me-index-div">
      <div class="me-index-header">
        <div class="head-img-div">
          <span class="head-poem">{{poem1}}<br>{{poem2}}</span>
          <img class="head-img" :src="headerImgSrc">
        </div>
      </div>
      <div class="me-index-body scrollbar">
        <!--  -->
        <Row style="background:rgba(238,238,238,0.5);padding:20px">
          <Col span="11">
            <Card shadow>
              <p slot="title" class="me-index-title-font"></p>
              <p class="me-index-content-font">CodeZero</p>
              <p class="me-index-content-font">JavaJavaScriptHTML</p>
              <p class="me-index-content-font"></p>
              <p class="me-index-content-font">nodeVueAngular</p>
            </Card>
          </Col>
          <Col span="11" offset="2">
            <Card shadow>
              <p slot="title" class="me-index-title-font"></p>
              <p class="me-index-content-font">CodeZer0</p>
              <p class="me-index-content-font"></p>
              <p class="me-index-content-font">Email739230137@qq.com</p>
              <p class="me-index-content-font">GitHubhttps://github.com/SakuraGakki</p>
            </Card>
          </Col>
        </Row>
        <!--  -->
        <Row style="background:rgba(238,238,238,0.5);padding:20px">
          <Col span="24">
            <Card shadow>
              <p slot="title" class="me-index-title-font"></p>
              <p class="me-index-content-font">
                </p>
            </Card>
          </Col>
        </Row>
        <!--  -->
        <Row style="background:rgba(238,238,238,0.5);padding:20px">
          <Col span="24">
            <Card shadow>
              <p slot="title" class="me-index-title-font"></p>
              <Row>
                <Col span="6" v-for="item in wechatArticleList">
                  <Card>
                    <div style="text-align: center">
                      <img :src="item.imgUrl" style="width: 250px;height: 100px"/>
                      <h3><a :href="item.url" target="_blank">{{item.title}}</a></h3>
                    </div>
                  </Card>
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
      <br>
      </div>
    </div>
  </div>
</template>

<script>
  import $ajax from 'axios'
  import Vue from "vue";

  const poemsArray = [
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""],
    ["",""]
  ];
  export default {
    name: "MeIndex",
    data() {
      return {
        ip:"",
        city:"",
        poem1:"",
        poem2:"",
        headerImgSrc:"http://59.110.218.235/images/avator.jpeg",
        wechatArticleList:[]
      }
    },
    created() {
      this.getPoem();
      this.getWechatArticleList()
      this.getClientInfo()
    },
    methods:{
      //
      getClientInfo(){
        if(localStorage.hasOwnProperty("ip") && localStorage.hasOwnProperty("city")){
          this.ip = localStorage.getItem("ip")
          this.city = localStorage.getItem("city")
        }else{
          $ajax.get('http://myip.ipip.net/',{}).then(
            res => {
              res.data = res.data.substring(6,res.data.length)
              this.ip = res.data.substring(0,res.data.indexOf("  "))
              this.city = res.data.substring(res.data.indexOf("  ")).replace("  ","")
              localStorage.setItem("ip",this.ip)
              localStorage.setItem("city",this.city)
              this.uptVisitorNum()
            }
          )
        }
        this.uptVisitorNum()
      },
      //
      uptVisitorNum(){
        $ajax.post(
          Vue.config.BaseUrl.ip + Vue.config.BaseUrl.uptVisitNum,
          {
            "ip":this.ip,
            "city":this.city
          },
          {}
        ).then(data => {
          // console.log("",data)
          if(data.data.status === 0){
            localStorage.setItem("ip",this.ip)
            localStorage.setItem("city",this.city)
          }
        })
      },
      //
      getPoem(){
        let random = Math.floor(Math.random()*10);
        this.poem1 = poemsArray[random][0];
        this.poem2 = poemsArray[random][1];
      },
      //cms
      getWechatArticleList(){
        $ajax.post('/cms/wechat/getWechatArticleList').then(res=>{
          if(res.data.status == 0){
            this.wechatArticleList = res.data.data
          }
        })
      },

    }
  }
</script>

<style scoped>
  .me{
    background-image: url("../../assets/img/me/background.jpg");
    background-size:100% 100%;
    background-repeat:no-repeat;
  }
  .me-index-div {
    margin: 0 auto;
    width: 1200px;
    height: calc(100vh);
    box-shadow: 2px 2px 10px #909090;
  }

  .me-index-header {
    width: 100%;
    height: 180px;
    background-image: url("../../assets/img/me/bg1.jpg");
  }

  .head-img-div {
    position: absolute;
    width: 200px;
    height: 200px;
    border-radius: 200px;
    background-color: #3e3b3c;
    z-index: 1;
    left: 50%;
    top: 200px;
    transform: translate(-50%, -50%);
  }

  .head-img {
    width: 200px;
    height: 200px;
    border-radius: 200px;
  }

  .head-poem{
    display:block;
    position: absolute;
    right:-476px;
    top:0px;
    font-family: "";
    font-size: 36px;
    /**/
    /*writing-mode: vertical-rl;!*  writing-mode: vertical-rl;*!*/
    height:260px;
    color:#FFFFFF;
  }

  .me-index-body {
    width: 100%;
    height:calc(100vh - 210px);
    overflow: auto;
    background-color: rgba(0, 0, 0, 0);
  }

  .me-index-title-font {
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
    font-size: 20px;
  }

  .me-index-content-font {
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
    font-size: 20px;
  }

  .scrollbar::-webkit-scrollbar {/**/
    display: none;  /**/
    width: 5px;     /**/
    height: 1px;
  }
  .scrollbar::-webkit-scrollbar-thumb {/**/
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #535353;
  }
  .scrollbar::-webkit-scrollbar-track {/**/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #EDEDED;
  }
</style>



